<template>
  <div class="vediowatch text-center">
    <main>
      <!--- POINTS OF INTEREST --->
      <div class="POI-graphic">
        <section class="POI-wrapper" id="POI-wrapper">
          <div class="POI-container">
            <div class="POI-point-container">
              <video-player
                class="video-player-box"
                ref="videoPlayer"
                :options="playerOptions"
              >
              </video-player>
              <div class="text-center">
                <button class="btn btn-group">
                  {{ vedio.tags }}
                </button>
              </div>
              <div class="POI-more-content active">
                <h2>{{ vedio.name }}</h2>
                <p>
                  {{ vedio.description }}
                </p>
              </div>
            </div>
          </div>
        </section>
      </div>
    </main>
  </div>
</template>
<script>
import "video.js/dist/video-js.css";

import { videoPlayer } from "vue-video-player";

export default {
  components: { videoPlayer },
  name: "vediowatch",
  computed: {
    vedio() {
      return this.$store.state.vedios.find(vid => {
        return vid.id == this.$route.params.id;
      });
    },
    playerOptions() {
      return {
        // videojs options
        muted: true,
        language: "en",
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        sources: [
          {
            type: "video/mp4",
            src: this.vedio.vediourl
          }
        ],
        poster: this.vedio.image
      };
    }
  }
};
</script>
<style>
.video-js {
  width: 100% !important;
}
main {
  min-height: calc(100vh + 60px);
}
/*GENERAL*/
* {
  font-family: "Roboto", sans-serif;
}
h2 {
  color: #003a79;
  font-weight: 600;
  font-size: 28px;
}
p {
  color: #545454;
}
.POI-graphic {
  max-width: 960px;
  margin: auto;
  padding: 30px;
}
.liveDemo__link {
  font-weight: 600;
  color: #003a79 !important;
  font-size: 20px;
  max-width: 700px;
  margin: 8px auto;
  padding: 0 30px;
  display: block;
}

/* POINTS OF INTEREST SECTION*/
.POI-graphic {
  min-height: 0px;
}
.POI-wrapper {
  max-width: 700px;
  margin: auto;
  height: 100%;
  transition: height 0.3s ease-out;
  -o-transition: height 0.3s ease-out;
  -webkit-transition: height 0.3s ease-out;
  height: 548px;
  margin-bottom: 30px;
}

/*... Touch point image ...*/
.POI-container img {
  width: 200px;
}
/*... The actual touch point...*/
.POI-point {
  position: absolute;
  display: block;
  width: 40px;
  height: 40px;
  border-radius: inherit;
  background: #545454;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.5), inset 0 1px 0 rgba(255, 255, 255, 0.3);
  z-index: 2;
  -webkit-transition: background-color 0.3s;
  -moz-transition: background-color 0.3s;
  transition: background-color 0.3s;
  z-index: 5;
  transition: transform 0.3s ease-in-out;
  -moz-transition: transform 0.3s ease;
  -o-transition: transform 0.3s ease;
  -webkit-transition: transform 0.3s ease;
  border-radius: 50%;
  overflow: hidden;
  white-space: nowrap;
  cursor: pointer;
}
/*... Individual points ...*/
.point-1 {
  top: 30%;
  left: 27%;
}
.point-1::before {
  content: "1";
  position: absolute;
  color: #fff;
  line-height: 40px;
  text-align: center;
  width: 40px;
}
.point-2 {
  top: 20%;
  left: 36%;
}
.point-2::before {
  content: "2";
  position: absolute;
  color: #fff;
  line-height: 40px;
  text-align: center;
  width: 40px;
}
.point-3 {
  top: 40%;
  left: 44%;
}
.point-3::before {
  content: "3";
  position: absolute;
  color: #fff;
  line-height: 40px;
  text-align: center;
  width: 40px;
}
.point-4 {
  top: 30%;
  left: 53%;
}
.point-4::before {
  content: "4";
  position: absolute;
  color: #fff;
  line-height: 40px;
  text-align: center;
  width: 40px;
}
.point-5 {
  top: 50%;
  left: 59%;
}
.point-5::before {
  content: "5";
  position: absolute;
  color: #fff;
  line-height: 40px;
  text-align: center;
  width: 40px;
}
.point-6 {
  top: 40%;
  left: 67%;
}
.point-6::before {
  content: "6";
  position: absolute;
  color: #fff;
  line-height: 40px;
  text-align: center;
  width: 40px;
}
/*... More Content ...*/
.POI-more-content {
  visibility: hidden;
  opacity: 0;
  top: 200px;
  transition: 0.3s height ease-out, 0.3s visibility ease, 0.3s opacity ease;
  -o-transition: 0.3s height ease-out, 0.3s visibility ease, 0.3s opacity ease;
  -webkit-transition: 0.3s height ease-out, 0.3s visibility ease,
    0.3s opacity ease;
  -moz-transition: 0.3s height ease-out, 0.3s visibility ease, 0.3s opacity ease;
}
.active {
  display: block;
  visibility: visible;
  opacity: 1;
}
.POI-point.active {
  background: #fe8d0b;
}
.POI-point:hover {
  transform: scale(1.2);
  -ms-transform: scale(1.2);
  -webkit-transform: scale(1.2);
}
</style>
